import { userService } from "../../services/user.service.js"


export function login(credentials) {
    return (dispatch) => {
        userService.login(credentials)
            .then(user => {
                dispatch({ type: 'SET_USER', user })
                console.log('Hello', user.username)
            })
            .catch(err => {
                console.error('Oops:', err)
                console.log('Error, couldnt login')
            })            
        }
    }
    
    export function signup(credentials) {
        return (dispatch) => {
            userService.signup(credentials)
            .then(user => {
                console.log('Welcome, ', user.username)
                dispatch({ type: 'SET_USER', user })
            })
            .catch(err => {
                console.error('Oops:', err)
            })
            
        }
    }
    
    export function logout() {
        return (dispatch) => {
            userService.logout()
            .then(() => {
                console.log('goodbye, ')
                dispatch({ type: 'SET_USER', user: null })
            })
            .catch(err => {
                console.error('Oops:', err)
            })

    }
}

export function checkout() {
    return (dispatch, getState) => {

        const { shoppingCart } = getState().carModule
        const amount = shoppingCart.reduce((acc, car) => acc + car.price, 0)


        userService.updateBalance(-amount)
            .then((balance) => {
                dispatch({ type: 'SET_USER_BALANCE', balance })
                dispatch({ type: 'CLEAR_CART' })
                console.log('Charged you: $' + amount.toLocaleString())
            })
            .catch(err => {
                console.error('Oops:', err)
                console.log('Cannot checkout')
            })

    }
}