import { Link } from 'react-router-dom'
export function ToyPreview({ toy, onRemoveToy }) {

    // const toyStyle = { backgroundImage: `url(https://robohash.org/${toy._id}?set=set4)` }
    return (
        <div className='toy-preview'>
            <Link to={`/toy/${toy._id}`}>
                <div className='info'>
                    <img src={`https://robohash.org/${toy._id}?set=set4`} alt="" />
                    <h2 className='toy-preview-name'>{toy.name}</h2>
                    <h4 className='toy-preview-price'>${toy.price}</h4>
                </div>
            </Link>
            <section>
                <button onClick={(ev) => onRemoveToy(toy._id, ev)}>Delete</button>
            </section>
        </div>
    )
}
