
const INITIAL_STATE = {
    toys: null,
    filterBy: null
}


export function toyReducer(state = INITIAL_STATE, action) {

    switch (action.type) {
        case 'SET_TOYS':
            return {
                ...state,
                toys: action.toys
            }
        case 'REMOVE_TOY':
            return {
                ...state,
                toys: state.toys.filter(toy => toy._id !== action.toyId)
            }
        case 'SET_FILTER_BY':
            return {
                ...state,
                filterBy: { ...action.filterBy }
            }
        default:
            return state;
    }
}