import { storageService } from './async-storage.service.js'
import { httpService } from './http.service.js'

const BASE_URL = 'user/'
const STORAGE_KEY_LOGGEDIN = 'loggedinUser'

export const userService = {
    login,
    logout,
    signup,
    getLoggedinUser,
    updateBalance
}

window.us = userService

function login(credentials) {
    console.log('credentials',credentials);
    return httpService.get(BASE_URL).then(users => {
        const user = users.find(user =>
            user.username === credentials.username &&
            user.password === credentials.password)
        if (user) sessionStorage.setItem(STORAGE_KEY_LOGGEDIN, JSON.stringify(user))
        return user
    })
}

function signup(userInfo) {
    userInfo.balance = 10000
    return httpService.post(BASE_URL+ userInfo)
        .then((user) => {
            sessionStorage.setItem(STORAGE_KEY_LOGGEDIN, JSON.stringify(user))
            return user
        })
}

function logout() {
    sessionStorage.setItem(STORAGE_KEY_LOGGEDIN, null)
    return Promise.resolve()
}

function getLoggedinUser() {
    return JSON.parse(sessionStorage.getItem(STORAGE_KEY_LOGGEDIN))
}

function updateBalance(diff) {
    const user = userService.getLoggedinUser()
    if (!user) return Promise.reject('Login first')

    if (user.balance + diff < 0 ) return Promise.reject('Not enough balanace')

    user.balance += diff
    return httpService.put(BASE_URL, user)
        .then((user) => {
            sessionStorage.setItem(STORAGE_KEY_LOGGEDIN, JSON.stringify(user))
            return user.balance
        })
}


