import { useEffect, useState } from 'react'
import { useRef } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import { useForm } from '../hooks/useForm'
import { toyService } from '../services/toy.service'

export const ToyEdit = () => {

    const params = useParams()
    const navigate = useNavigate()

    const [toy, handleChange, setToy] = useForm({
        name: '',
        price: ''
    })

    const inputRef = useRef()

    useEffect(() => {
        inputRef.current.focus()
        const toyId = params.toyId
        if (!toyId) return
        toyService.getById(toyId)
            .then(toy => {
                setToy(toy)
            })
            .catch(err => {
                console.log('err:', err);
            })
    }, [])

    const onSaveToy = (ev) => {
        ev.preventDefault()
        toyService.save({ ...toy }).then(() => {
            navigate('/toy')
        })
    }
    
    const onBack = () => {
        navigate('/toy')
    }

    return (
        <section className='toy-edit'>
            <h1>{toy._id ? 'Edit' : 'Add'} Toy</h1>
            <form onSubmit={onSaveToy}>
                <label htmlFor="name">Name</label>
                <input ref={inputRef} value={toy.name} onChange={handleChange} name="name" id="name" />
                <label htmlFor="price">Price {toy.price ? toy.price : 500}</label>
                <input type="range" value={toy.price} onChange={handleChange} min="0" max="1000" step="1" name="price" id="price" required="required" />


                <button>Save</button>
            </form>
            <button onClick={onBack}>Back to Toys App</button>
        </section>
    )
}
