import { storageService } from './async-storage.service.js'
import { httpService } from './http.service.js'

export const toyService = {
    query,
    save,
    remove,
    getById,
}

const BASE_URL = 'toy/'

function query(filterBy) {
    return httpService.get(BASE_URL, { params: filterBy }).then(toys => {
        // if (filterBy) {
        //     let { name, inStock } = filterBy
        //     toys = toys.filter(toy => toy.name.toUpperCase().includes(name.toUpperCase()) &&
        //         toy.inStock === inStock)
        // }
        return toys
    })
}

function getById(toyId) {
    return httpService.get(BASE_URL + toyId)
        .then(toy => {
            toy.reviews = ['Great toy', 'Funny toy']
            return toy
        })
}

function remove(toyId) {
    return httpService.delete(BASE_URL + toyId)
        .then(res => res)
}

function save(toy) {
    if (toy._id) {
        return httpService.put(BASE_URL + toy._id, toy)
            .then(res => res)
    } else {
        toy.createdAt = Date.now()
        toy.labels = ["Doll", "Battery Powered"]
        toy.inStock = true
        return httpService.post(BASE_URL, toy)
            .then(res => res)
    }
}